@media screen and (min-width: 768px) {
  .modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 450px;
  }

  .backdrop.is-hidden .modal {
    transform: translate(-50%, 150%) scale(0.75);
  }
}
@media screen and (min-width: 1200px) {
  .modal {
    width: 528px;
    margin: 0;
  }
}
